
import { defineComponent, PropType, ref } from "vue";

export default defineComponent({
  name: "MyCustomFieldTwo",
  props: {
    modelValue: {
      type: Array as PropType<string[]>,
    },
    onlyOne: {
      type: Boolean,
      default: false,
      required: false,
    },
    readOnly: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    const addDialIsVisible = ref(false);
    const locList = ref(props.modelValue);
    const selvalue = ref("");
    const options = ["Field1", "Field2", "Field3", "Field4"];

    const openFindDial = () => {
      addDialIsVisible.value = true;
    };

    const handleDelete = (tag: string) => {
      locList.value.splice(locList.value.indexOf(tag), 1);
      emit("update:modelValue", locList.value);
    };

    const selected = (val: string) => {
      locList.value.push(val);
      emit("update:modelValue", locList.value);
      addDialIsVisible.value = false;
    };
    return {
      addDialIsVisible,
      locList,
      selvalue,
      options,
      handleDelete,
      openFindDial,
      selected,
    };
  },
});
